<template>
  <!-- 布局页面 -->
  <div class="toplevel-page">
    <div class="toplevel-top"></div>
    <!-- 下部内容 -->
    <div class="toplevel-bottom">
      <div class="toplevel-bottom-layout">
        <!-- 左侧 -->
        <div class="toplevel-left">
          <div class="toplevel-query-box">
            <!-- 输入框 -->
            <input
              type="text"
              v-model="queryCenter"
              placeholder="输入查询内容"
              class="query-input"
            />
            <!-- 信息展示框-->
            <div class="exhibition-box">
              <p
                class="exhibition-item"
                v-for="(item, index) in matchTechnolog"
                :key="index"
              >
                <span @click="seeTechnolog(index)"> {{ index }}</span>
              </p>
            </div>
          </div>
        </div>
        <!-- 右侧区域 -->
        <div class="toplevel-right">
          <pre>{{ rightShowData }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, watch } from 'vue';
import { changeEleSize } from '@/utils/common.js';
import { allTechnologyList } from './technology/index.js';

export default {
  props: {},
  name: 'toplevel',

  setup() {
    const data = reactive({
      detailsbox: null, //保存要移动的元素
      outputData: ``, //右侧最终展示结果
      queryCenter: '', //检索词
      matchTechnolog: allTechnologyList, //文本
      rightShowData: '' //右侧显示数据
    });
    watch(
      // 监听输入框
      () => data.queryCenter,
      () => {
        retrievalTechnology();
      }
    );
    onMounted(() => {
      monitorMouse(); //监听鼠标移动
      window.onresize = () => {
        history.go(0); //页面尺寸变化刷新页面
      };
    });
    // 检索词变化重新创建列表对对象
    const retrievalTechnology = () => {
      data.matchTechnolog = {};
      for (let key in allTechnologyList) {
        if (key.includes(data.queryCenter)) {
          data.matchTechnolog[key] = allTechnologyList[key];
        }
      }
    };
    // 查看详情
    const seeTechnolog = (index) => {
      data.rightShowData = allTechnologyList[index];
    };

    // 监听鼠标移动,改变元素尺寸（宽度）
    const monitorMouse = () => {
      const visualW = document.body.offsetWidth; //当前浏览器可视宽度
      if (visualW < 600) {
        //宽度小于600不可拖拽
        return;
      } else {
        const eleInside = document.getElementsByClassName('toplevel-bottom')[0]; //需要拖拽元素的父父级
        const showBox = document.getElementsByClassName(
          'toplevel-bottom-layout'
        )[0]; //需要拖拽元素的父级
        const eleL = document.getElementsByClassName('toplevel-left')[0]; //左侧元素
        const eleR = document.getElementsByClassName('toplevel-right')[0]; //左侧元素
        const body = document.querySelector('body'); //获取body改变鼠标样式
        // 监听抬起事件，防止在拖拽区域点击，按下同时移出的bug
        document.onmouseup = function () {
          showBox.onmousemove = null;
        };
        // 监听鼠标移动事件，指定范围范围内改变样式，指定拖拽
        eleInside.onmousemove = function (e) {
          const eleWidth = parseInt(getComputedStyle(eleL).width) - 5; //获取左侧元素右侧位置 -5扩大范围
          //给元素增加移动事件（左侧元素和右侧元素之间就可拖拽）
          if (e.pageX > eleWidth && e.pageX < eleWidth + 10) {
            body.style.cursor = 'w-resize'; //改变鼠标为拖拽样式
            eleInside.onmousedown = function (e) {
              //按下执行拖拽
              changeEleSize(eleL, eleR, showBox, visualW);
            };
          } else {
            body.style.cursor = 'default'; //默认样式
            eleInside.onmousedown = null; //清除按下事件
          }
        };
      }
    };
    return {
      seeTechnolog,
      ...toRefs(data)
    };
  }
};
</script>
