export const jsList = {
  ['axios发送多次重复请求,中断上一次']: `
  // api.js接口文件中定义
  import axios from 'axios';   //引入axios
  export const queryCollectionInfoData = (data, that) =>
  service({
    url: '/ user / queryCollectionInfoData',
    ...base(data),
    cancelToken: new axios.CancelToken(function executor(c) {    // 设置 cancel token 用于多次请求，中断上一次请求
      that.source = c;    //传递this增加source属性
    }),
  });

  // vue组件中使用
  data(){
    return{
      source: null, // axios 请求中断
    }
  }
  // 请求的方法
  executeRequest(){
    // 这里是中断上一个请求  防止多次请求数据显示错误
    if (this.source) {
      this.source();
    }
    // 调用接口
    queryCollectionInfoData(requestData, this)
    .then((res) => {
      //成功
    })
    .catch((err) => {
      中断取消的提示
      if (axios.isCancel(err)) {
        console.log(axios.isCancel(err), '(axios.isCancel(err)');
      }
    });
  }
  
  `


  

}
